<template lang="html">
  <div class="listview">
    <div class="loading-wrapper" v-show="data.length == 0">
      <loading :scale="1.5"></loading>
    </div>
    <scroller
      class="scroller"
      :data="data"
      :onScroll="onScroll"
      ref="scroller">
      <ul>
        <li v-for="group in data" class="list-group" ref="groupList">
          <h2 class="list-group-title">{{ group.title }}</h2>
          <ul>
            <li v-for="item in group.items" class="list-group-item" @click.capture="select(item)">
              <img v-lazy="item.avatar" class="avatar">
              <span class="name">{{ item.name }}</span>
            </li>
          </ul>
        </li>
      </ul>
    </scroller>
    <div class="list-shortcut"
      @touchstart="onShortcutTouchstart"
      @touchmove.stop.prevent="onShortcutTouchmove">
      <ul>
        <li
          v-for="(shortcut, index) in shortcutList"
          class="item" :data-index="index"
          :class="{ current: index === currentIndex }">
          {{ shortcut }}
        </li>
      </ul>
    </div>
    <div class="list-fixed" ref="fixed" v-show="fixedTitle && !hideFixedTitle">
      <div class="fixed-title">{{fixedTitle}} </div>
    </div>
  </div>
</template>

<script>
import Scroller from 'components/base/scroller/scroller'
import Loading from 'components/base/loading/loading3'
import { timeout } from 'assets/js/util'
import { catchErrors } from 'api/handler'

export default {
  name: 'listview',
  props: {
    data: {
      type: Array,
      default: []
    },
    selectItem: Function
  },
  components: { Scroller, Loading },
  created () {
    // 这里创建的组件属性不会被watch
    this.touch = {
      originalIndex: 0,
      y: 0,
      anchorItemHeight: 18
    }
    this.groupHeightList = []
  },
  data () {
    return {
      currentIndex: 0,
      hideFixedTitle: false
    }
  },
  computed: {
    shortcutList () {
      return this.data.map(group => group.title.substr(0, 1))
    },
    fixedTitle () {
      return this.data[this.currentIndex] ? this.data[this.currentIndex].title : ''
    }
  },
  watch: {
    data: catchErrors(async function () {
      await timeout(50) // wait for dom render
      this._caculateHeight()
    })
  },
  methods: {
    /**
     * 按下anchor，记录当时的index和垂直位置y
     */
    onShortcutTouchstart (e) {
      const el = e.target
      const touch = e.touches[0]
      const { index } = el.dataset
      this.touch.y = touch.pageY
      if (!index) return
      this.touch.anchorItemHeight = el.clientHeight
      this.touch.originalIndex = +index
      this._scrollToIndex(this.touch.originalIndex)
    },
    /**
     * 在anchor上按住并移动，计算跟最开始按下时的距离算出变化的index值delta
     */
    onShortcutTouchmove (e) {
      const touch = e.touches[0]
      const delta = Math.floor((touch.pageY - this.touch.y) / this.touch.anchorItemHeight)
      this._scrollToIndex(this.touch.originalIndex + delta)
    },
    /**
     * 调用scroller内置方法scrollToElement移动到对应的index
     */
    _scrollToIndex (index) {
      const { scroller, groupList } = this.$refs
      if (index < 0 || index > groupList.length - 1) return
      this.currentIndex = index
      scroller.scrollToElement(groupList[index], 0)
    },
    /**
     * 滚动时的回调
     */
    onScroll ({ y }) {
      if (y >= 0) {
        this.hideFixedTitle = true
      } else {
        this.hideFixedTitle = false
      }
      const index = this.groupHeightList.findIndex(groupHeight => groupHeight >= -y)
      this.currentIndex = index < 0 ? 0 : index
    },
    /**
     * 计算每个group的高度
     */
    _caculateHeight () {
      this.groupHeightList = []
      this.$refs.groupList.reduce((curr, next) => {
        const height = curr + next.clientHeight
        this.groupHeightList.push(height)
        return height
      }, 0)
    },
    /**
     * 选中一项
     */
    select (item) {
      if (this.selectItem) this.selectItem(item)
    },
    refresh () {
      this.$refs.scroller.refresh()
    }
  }
}
</script>

<style lang="less" scoped>
@import "~assets/less/variable";

.listview {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: @color-background;
  .scroller {
    width: 100%;
    height: 100%;
  }
  .list-group {
    padding-bottom: 30px;
    .list-group-title {
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      font-size: @font-size-small;
      color: @color-text-l;
      background: @color-highlight-background;
    }
    .list-group-item {
      display: flex;
      align-items: center;
      padding: 20px 0 0 30px;
      .avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
      .name {
        margin-left: 20px;
        color: @color-text-l;
        font-size: @font-size-medium;
      }
    }
  }
  .list-shortcut {
    user-select: none;
    position: absolute;
    z-index: 30;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    padding: 10px 0;
    border-radius: 10px;
    text-align: center;
    background: @color-background-d;
    font-family: Helvetica;
    .item {
      padding: 3px;
      line-height: 1;
      color: @color-text-l;
      font-size: @font-size-small;
      &.current {
        color: @color-theme;
      }
    }
  }
  .list-fixed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    .fixed-title {
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      font-size: @font-size-small;
      color: @color-text-l;
      background: @color-highlight-background;
    }
  }
  .loading-wrapper {
    margin-top: 20px;
    text-align: center;
  }
}

</style>



