<template lang="html">
  <div class="singer" ref="singer">
    <listview :data="singerGroupList" :selectItem="selectSinger" ref="listview"></listview>
    <router-view></router-view>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import * as atypes from '@/store/action-types'
import Listview from 'components/base/listview/listview'
import PlayerMixin from '@/mixins/player-mixin'
import { miniPlayerHeight } from 'assets/js/config'

export default {
  name: 'singer',
  components: { Listview },
  mixins: [PlayerMixin],
  created () {
    this._getSingerList()
  },
  computed: {
    ...mapGetters([
      'singerGroupList'
    ])
  },
  methods: {
    ...mapActions({
      _getSingerList: atypes.GET_SINGER_LIST
    }),
    selectSinger (singer) {
      this.$router.push(`/singer/${singer.id}`)
    },
    handlePlaylist (playlist) {
      const bottom = playlist.length > 0 ? miniPlayerHeight : 0
      this.$refs.singer.style.bottom = bottom
      this.$refs.listview.refresh()
    }
  }
}
</script>

<style scoped lang="less">
  .singer {
    position: absolute;
    top: 88px;
    bottom: 0;
    width: 100%;
  }
</style>



