import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters([
      'playlist'
    ])
  },
  mounted () {
    this.handlePlaylist(this.playlist)
  },
  activated () {
    this.handlePlaylist(this.playlist)
  },
  watch: {
    playlist () {
      this.handlePlaylist(this.playlist)
    }
  },
  methods: {
    handlePlaylist () {
      throw new Error('Component must implements handlePlaylist method')
    }
  }
}



